import React,{Component} from 'react'
import {Input,Table,Button,Popconfirm,message} from 'antd'
import {connect} from 'react-redux'
import {getAllAdmin,addAdmin,deleteAdmin} from '../../reducers/login.redux'
import './index.css'

@connect(
  state => state.loginReducers,
  {getAllAdmin,addAdmin,deleteAdmin}
)
class AddAdmin extends Component{
  constructor(){
    super()
    this.state = {
      username:'',
      pwd:''
    }
  }
  componentWillMount(){
    //get all admin
    this.props.getAllAdmin()
  }
  confirm(){
    // add new admin
    this.props.addAdmin(this.state.username,this.state.pwd)
    this.setState({username:'',pwd:''})
    message.success('')
  }
  delete(id){
    //delete a admin
    this.props.deleteAdmin(id)
    message.success('')
  }
  handleChange(key,val){
    this.setState({[key]:val})
  }
  getColumns(){
    return[
      {
      title: '',
      dataIndex: 'username',
      key: 'username',
      render: text => <span>{text}</span>,
    },
    {
      title: '',
      key: 'action',
      render: (text, record) => (
        <Popconfirm
        title=""
        onConfirm={this.delete.bind(this,record._id)}
        okText=""
        cancelText=""
        >
          <span className="delete-admin"></span>
        </Popconfirm>
      ),
    }]
  }
  render(){
    return(
      <div id="addadmin-container">
        <p className="title"></p>
        <p className="add-admin"></p>
        <span>:</span>
        <Input className="add-admin-input" placeholder="" value={this.state.username} onChange={e => this.handleChange('username',e.target.value)}/>
        <span>:</span>
        <Input className="add-pwd-input" placeholder="" value={this.state.pwd} onChange={e => this.handleChange('pwd',e.target.value)}/>
        <br/>
        <Popconfirm
        title=""
        onConfirm={this.confirm.bind(this)}
        okText=""
        cancelText=""
        >
          <Button type="primary" className="add-admin-btn"></Button>
        </Popconfirm>
        <p className="now-admin"></p>
        {this.props.admin?<Table rowKey={record => record._id} columns={this.getColumns()} dataSource={this.props.admin} pagination={false}/>:null}
      </div>
    )
  }
}

export default AddAdmin
